@import '../mixin/link-btn';
@import '../mixin/page-title';
@import '../mixin/scroll-area';
@import '../mixin/plus-icon';

$black-font:#262626;
$gley-font: #88909b;


.single-title{
  height: 100vh;
  background-image: url(/img/IMG_7537.jpg);
  background-size: cover;
  background-position: center 0;
  position: relative;
  &::after{
    position: absolute;
    content: "";
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    left: 0;
    top: 0;
  }
  &__container{
    position: absolute;
    top:50%;
    padding: 0 25% 0 6.588%;
    color:white;
    z-index: 1;
  }
  &__time, &__category{
    font-family: 'Barlow Condensed', sans-serif;
    display: inline-block;
    padding: 0 0 35px;
    font-size: 1.6rem;
    font-weight: 600;
    letter-spacing: 0.25em;
  }
  &__category{
    padding-left: 20px;
  }
  &__name{
    font-size: 3.5rem;
    font-weight: 600;
    letter-spacing: 0.20em;
    overflow: hidden;
    line-height: 1.75;
  }
  &__author{
    padding: 30px 0 0;
    font-size: 1.6rem;
  }
  .scroll{
    @include _scroll-area(95%, 9%);
    z-index: 10;
    color:white;
  }
}