.single-title {
  height: 100vh;
  background-image: url(/img/IMG_7537.jpg);
  background-size: cover;
  background-position: center 0;
  position: relative;
}

.single-title::after {
  position: absolute;
  content: "";
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  left: 0;
  top: 0;
}

.single-title__container {
  position: absolute;
  top: 50%;
  padding: 0 25% 0 6.588%;
  color: white;
  z-index: 1;
}

.single-title__time, .single-title__category {
  font-family: 'Barlow Condensed', sans-serif;
  display: inline-block;
  padding: 0 0 35px;
  font-size: 1.6rem;
  font-weight: 600;
  letter-spacing: 0.25em;
}

.single-title__category {
  padding-left: 20px;
}

.single-title__name {
  font-size: 3.5rem;
  font-weight: 600;
  letter-spacing: 0.20em;
  overflow: hidden;
  line-height: 1.75;
}

.single-title__author {
  padding: 30px 0 0;
  font-size: 1.6rem;
}

.single-title .scroll {
  width: 12px;
  height: 300px;
  position: absolute;
  top: 95%;
  right: 9%;
  transform: translateY(-45%);
  z-index: 10;
  color: white;
}

.single-title .scroll p {
  position: absolute;
  top: 0;
  left: 12px;
  font-family: 'Barlow Condensed', sans-serif;
  font-weight: 500;
  font-size: 1.2rem;
  letter-spacing: 0.3em;
  transform: rotate(90deg);
  transform-origin: left top;
  line-height: 1;
  z-index: 9999;
}

.single-title .scroll__bar--1, .single-title .scroll__bar--2 {
  display: block;
  position: absolute;
  bottom: 0px;
  left: 6px;
  width: 1px;
  height: 230px;
  background: rgba(51, 51, 51, 0.1);
  pointer-events: none;
}

.single-title .scroll__bar--2 {
  background: rgba(51, 51, 51, 0.4);
  transform: scaleY(0);
  animation: scroll-bar 3s ease-in-out infinite forwards;
}
